import React from 'react';
// import Radium from 'radium';
import './servises.scss';
import photo1 from '../../img/service1.png';
import photo2 from '../../img/service2.png';
import photo3 from '../../img/service2.png';
import photo4 from '../../img/service1.png';
import photo5 from '../../img/service2.png';


import { Transition} from 'react-transition-group';


  class Servises extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isToggleOn: false,
        info: [
          { 
            id: photo1 ,
            title: '',
            text: 'Системы автоматического пожаротушения'
          },
          { 
            id: photo2 ,
            title: '', 
            text: 'Системы автоматической пожарной сигнализации'
          },
          { 
            id: photo3 ,
            title: '',
            text: 'Системы оповещения и управления эвакуацией людей при пожаре'
          },
          { 
            id: photo4 ,
            title: '', 
            text: 'Системы автоматической пожарной сигнализации'
          },
          { 
            id: photo5 ,
            title: '',
            text: 'Системы автоматического пожаротушения'
          },
          { 
            id: photo5 ,
            title: '',
            text: 'Системы оповещения и управления эвакуацией людей при пожаре'
          },
          { 
            id: photo4 ,
            title: '', 
            text: 'Системы автоматической пожарной сигнализации'
          },
          { 
            id: photo5 ,
            title: '',
            text: 'Системы автоматического пожаротушения'
          }
        ],
        otherInfo:[
          { 
            id: photo5 ,
            title: '',
            text: 'Системы автоматического пожаротушения'
          },
          { 
            id: photo5 ,
            title: '',
            text: 'Системы оповещения и управления эвакуацией людей при пожаре'
          },
          { 
            id: photo4 ,
            title: '', 
            text: 'Системы автоматической пожарной сигнализации'
          },
          { 
            id: photo5 ,
            title: '',
            text: 'Системы автоматического пожаротушения'
          }
        ]
      };
      this.handleClick = this.handleClick.bind(this);
    }
    handleClick() {
      this.setState(state => ({
        isToggleOn: !state.isToggleOn
      }));
    }

    

    render() {
      // if ((this.state.info.length & 2) !== 0) {
      //   console.log('even number')
      //   style.push()
      // }
      // else{
      //   console.log('not even number')
      // }
      // const evenClasses = [];
      // const stylo = {
        
      //     '@media(max-width: 1080px)':{
      //         display: 'grid',
      //         gridTemplateColumns: 'repeat(3, 2fr)'
      //         // ':nth-last-child(-n+2)'{
      //         //   display: 'none'
      //         // }
              
      //     }
        
        
      // }
      return (
        
        <div 
          className = 'servises' 
          id={this.props.id} 
          // style= {this.state.isToggleOn ? {height:'100%'}:{height: '100vh'}}
        >
          <div className = 'servisesTitle'>
            <h2>Наши услуги</h2>
          </div>
          <div className = 'servisesItem mainPartGlobal'>
          {this.state.info.map((item, index) => (
            
              <div key = {index} className = 'item mainPart' >
                <div className = 'itemFhoto'>
                  <img src={item.id} alt=""/>
                </div>
                <div className = 'textAboutItem'>
                      <p>{item.text}</p>
                    </div> 
              </div>
            
          ))}
          </div>
          
          <div className = 'buttonForMore'>   

          <Transition
            in = {this.state.isToggleOn}
            timeout={{
              enter: 1000,
              exit: 400
            }}
            mountOnEnter
            unmountOnExit
          > 
             {state => (<div className = 'servisesItem'>
                {this.state.otherInfo.map((item, index) => (
                  <div key = {index} className = {`item square ${state}`}>
                    <div className = 'itemFhoto'>
                      <img src={item.id} alt=""/>
                    </div>
                    <div className = 'textAboutItem'>
                      <p>{item.text}</p>
                    </div> 
                  </div>)
                )
                }
              </div>)} 
          </Transition>
              
              <button onClick = {this.handleClick} type = 'button' className = 'btnServises'>
                {this.state.isToggleOn ? 'Скрыть': 'Показать ещё'}
              </button>
              
          </div>
        </div>
      )
    }
  }
export default Servises;
// export default Radium(Servises);